export default {
  data() {
    return {
      activeNames: '1'
    }
  },
	onLoad() {
		uni.setNavigationBarTitle({
			title: '纳米氧化物光电实验室'
		})
	},
  methods: {
		/**
		 * 跳转页面
		 */
    handleClick: function() {
      console.log('跳转至巡查页面')
      uni.navigateTo({
      	url: './lab-approval',
      	success: res => {},
      	fail: () => {},
      	complete: () => {}
      });
    },
		/**
		 * 微信小程序用折叠面板变化方法
		 */
		collapseChange(event) {
			console.log(event);
			this.activeNames = event.detail
		}
  }
}
